body {
  font-size: 1.6 rem;
  font-family: "Open Sans", sans-serif;
  background: #fff;
  transition: all 0.3s ease;
}
body.darkmode {
  background: #000;
  color: #fff;
}

.boton {
  border: 2px solid #fff;
  border-radius: 15px;
  background: transparent;
  color: #fff;
  padding: 10px 15px;
  font-size: 15px;
  cursor: pointer;
}

.btn-amarillo {
  border: 2px solid rgba(255, 193, 0, 0.8784313725);
  background: rgb(255, 193, 0);
  background: -moz-radial-gradient(circle, rgba(255, 193, 0, 0.6474964986) 0%, rgb(255, 193, 0) 75%);
  background: -webkit-radial-gradient(circle, rgba(255, 193, 0, 0.6474964986) 0%, rgb(255, 193, 0) 75%);
  background: radial-gradient(circle, rgba(255, 193, 0, 0.6474964986) 0%, rgb(255, 193, 0) 75%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc100",endColorstr="#ffc100",GradientType=1);
}

.btn-amarillo:hover {
  background: rgb(255, 193, 0);
  background: -moz-radial-gradient(circle, rgb(255, 193, 0) 0%, rgba(255, 193, 0, 0.6474964986) 40%);
  background: -webkit-radial-gradient(circle, rgb(255, 193, 0) 0%, rgba(255, 193, 0, 0.6474964986) 40%);
  background: radial-gradient(circle, rgb(255, 193, 0) 0%, rgba(255, 193, 0, 0.6474964986) 40%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc100",endColorstr="#ffc100",GradientType=1);
}

.color_activo {
  color: #61CE70 !important;
}

.color_fondo {
  color: rgba(255, 193, 0, 0.8784313725);
}

.display_none {
  display: none !important;
}

.display_block {
  display: block !important;
}

.opacity_1 {
  opacity: 1 !important;
  transform: translate(0, 0) !important;
}

.opacity_50 {
  opacity: 0.5 !important;
  cursor: not-allowed !important;
}

.alerta {
  color: red !important;
}

.exito {
  color: green !important;
}

.borde__alerta {
  border: 2px red solid !important;
}

.borde__exito {
  border: 2px green solid !important;
}

.posicion_a {
  top: 35px;
}
.posicion_a .menu__izq .titulo__menu {
  color: #fff;
}
.posicion_a .menu__der li a {
  color: #fff;
}

.posicion_b {
  top: 0px;
}
.posicion_b .menu__izq .titulo__menu {
  color: #000;
}
.posicion_b .menu__der li a {
  color: #000;
}

.contenedor__menu {
  width: 100%;
  height: 35px;
  position: fixed;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  z-index: 101 !important;
  transition: all 0.3s ease;
}
.contenedor__menu .menu__izq {
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  padding-left: 55px;
}
.contenedor__menu .menu__izq .titulo__menu {
  height: 100%;
  margin: 0;
  line-height: 35px;
  vertical-align: center;
  font-weight: 800;
  font-size: 14px;
  font-style: italic;
}
.contenedor__menu .menu__izq .darkmode {
  margin-right: 15px;
  height: 25px;
  background: #343d5b;
  padding: 0 3px;
  border: none;
  border-radius: 1000px;
  position: relative;
  cursor: pointer;
  display: flex;
  outline: none;
}
.contenedor__menu .menu__izq .darkmode.active {
  background: orange;
}
.contenedor__menu .menu__izq .darkmode.active .img__darkmode {
  right: -2px;
  left: unset;
}
.contenedor__menu .menu__izq .darkmode .img__darkmode {
  height: 30px;
  width: 30px;
  display: block;
  position: absolute;
  top: -2px;
  left: -2px;
  right: unset;
  transition: all 0.3s ease;
}
.contenedor__menu .menu__izq .darkmode span {
  width: 25px;
  height: 25px;
  line-height: 25px;
  display: block;
  background: none;
  color: #fff;
}
.contenedor__menu input {
  display: none;
}
.contenedor__menu label {
  border-radius: 5px;
  font-size: 20px;
  padding: 1px 5px;
  cursor: pointer;
  position: absolute;
  right: 25px;
  top: 6px;
}
.contenedor__menu .icono_m {
  display: none;
  opacity: 1;
}
.contenedor__menu .icono_x {
  display: none;
  opacity: 1;
  font-size: 23px;
}
.contenedor__menu .menu__der {
  height: 100%;
  width: 60%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}
.contenedor__menu .menu__der li {
  height: 100%;
  list-style: none;
  display: flex;
  justify-content: center;
}
.contenedor__menu .menu__der li a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  font-weight: 600;
  font-size: 14px;
  text-decoration: none;
}

.portada {
  width: 100%;
  height: 100vh;
  background-image: url(../img/portada.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.portada:hover {
  background-position: bottom;
}
.portada body.darkmode .portada {
  background-image: url(../img/portada2.jpg);
}
.portada .contenedor {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.portada .contenedor h1 {
  font-weight: 700;
  font-size: 45px;
  font-style: italic;
}
.portada .contenedor h2 {
  font-size: 20px;
}
.portada .contenedor button {
  margin-top: 100px;
}
.portada .marco__sup {
  height: 20px;
  position: fixed;
  background: rgba(255, 193, 0, 0.4901960784);
  z-index: 100;
}
body.darkmode .portada .marco__sup {
  background: rgba(255, 193, 0, 0.8784313725);
}
.portada .marco__der {
  z-index: 101;
  width: 20px;
  position: fixed;
  background: rgba(255, 193, 0, 0.4901960784);
  top: 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body.darkmode .portada .marco__der {
  background: rgba(255, 193, 0, 0.8784313725);
}
.portada .marco__der a {
  text-decoration: none;
  color: #000;
  font-size: 6px;
  margin: 8px 0;
}
.portada .marco__inf {
  z-index: 101;
  height: 20px;
  position: fixed;
  background: rgba(255, 193, 0, 0.4901960784);
  display: flex;
  justify-content: center;
  align-items: center;
}
body.darkmode .portada .marco__inf {
  background: rgba(255, 193, 0, 0.8784313725);
}
.portada .marco__inf .inf_continua {
  font-size: 11px;
}
.portada .marco__inf .inf_continua span {
  margin: 0 10px;
}
.portada .marco__inf .inf_continua a {
  color: #fff;
  text-decoration: none;
}
body.darkmode .portada .marco__inf .inf_continua a {
  color: #000;
}
.portada .marco__inf .inf_inicio {
  font-size: 11px;
}
.portada .marco__inf .inf_inicio span {
  margin: 0 10px;
}
.portada .marco__inf .inf_inicio a {
  color: #fff;
  text-decoration: none;
}
body.darkmode .portada .marco__inf .inf_inicio a {
  color: #000;
}
.portada .marco__izq {
  z-index: 101;
  width: 20px;
  position: fixed;
  background: rgba(255, 193, 0, 0.4901960784);
  top: 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body.darkmode .portada .marco__izq {
  background: rgba(255, 193, 0, 0.8784313725);
}
.portada .marco__izq a {
  text-decoration: none;
  color: #000;
  font-size: 15px;
  margin: 8px 0;
}
.portada .sup_a {
  width: calc(100% - 30px);
  top: 15px;
}
.portada .sup_b {
  height: 35px;
  top: 0;
  right: 0;
  width: 100%;
}
.portada .der_a {
  height: calc(100% - 70px);
  right: 15px;
}
.portada .der_b {
  height: calc(100% - 55px);
  right: 0;
}
.portada .inf_a {
  width: calc(100% - 30px);
  bottom: 15px;
}
.portada .inf_b {
  left: 0;
  bottom: 0;
  width: 100%;
}
.portada .izq_a {
  height: calc(100% - 70px);
  left: 15px;
}
.portada .izq_b {
  height: calc(100% - 55px);
  left: 0;
}

.separador-section {
  width: 100%;
  height: 250px;
  background: rgba(255, 193, 0, 0.4901960784);
  position: relative;
  z-index: 10;
  margin-bottom: 50px;
}
.separador-section .tecnologias {
  padding: 15px 25px;
  display: grid;
  column-gap: 10px;
  grid-template-columns: repeat(3, 1fr);
}
.separador-section .tecnologias .tarjeta {
  display: flex;
  justify-content: center;
}
.separador-section .tecnologias .tarjeta .descripcion .titulo {
  font-size: 18px;
}
.separador-section .tecnologias .tarjeta .descripcion ul {
  position: relative;
  left: 0;
  top: -15px;
  list-style: none;
  margin: 0;
}
.separador-section .tecnologias .tarjeta .descripcion ul li {
  margin: 5px auto;
  position: relative;
  left: -35px;
  font-size: 15px;
}
.separador-section .iconos-tecnologias {
  display: grid;
  grid-template-columns: repeat(14, 1fr);
  column-gap: 15px;
  grid-template-rows: auto;
  margin-right: 2em;
}
.separador-section .iconos-tecnologias li {
  display: inline-block;
  margin: 0 auto;
  font-size: 30px;
}
.separador-section .iconos-tecnologias li i:hover {
  color: rgba(255, 193, 0, 0.8784313725);
}
.separador-section .efecto-ola {
  position: absolute;
  left: 20px;
  bottom: 0;
  width: calc(100% - 40px);
}
.separador-section .efecto-ola div {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
}
body.darkmode .separador-section .efecto-ola div .ola1 {
  fill: rgba(0, 0, 0, 0.2) !important;
}
body.darkmode .separador-section .efecto-ola div .ola2 {
  fill: rgba(0, 0, 0, 0.5) !important;
}
body.darkmode .separador-section .efecto-ola div .ola3 {
  fill: rgb(0, 0, 0) !important;
}

.sobreMi h3, .porfolio h3, .contacto h3 {
  line-height: 60px;
  text-align: center;
  vertical-align: middle;
  font-size: 60px;
  font-weight: 400;
}
.sobreMi h4, .porfolio h4, .contacto h4 {
  text-align: center;
  font-size: 30px;
  font-weight: 300;
  position: relative;
  top: -45px;
}
.sobreMi .contenedor-desc, .porfolio .contenedor-desc, .contacto .contenedor-desc {
  position: relative;
  top: -50px;
}
.sobreMi .contenedor-desc p, .porfolio .contenedor-desc p, .contacto .contenedor-desc p {
  width: 75%;
  margin: 15px auto;
  font-size: 18px;
}

.sobreMi {
  display: flex;
  flex-direction: column;
}
.sobreMi h3 span {
  color: rgba(255, 193, 0, 0.8784313725);
}
body.darkmode .sobreMi h3 span {
  color: rgba(255, 193, 0, 0.4901960784);
}
.sobreMi a {
  margin: auto;
  text-decoration: none;
}

.portfolio-titulo {
  margin: 0;
}
.portfolio-titulo h3 span {
  color: rgba(255, 193, 0, 0.8784313725);
}
body.darkmode .portfolio-titulo h3 span {
  color: rgba(255, 193, 0, 0.4901960784);
}
.portfolio-titulo h4 {
  margin: 15px 0;
  font-size: 20px;
}

.trabajos-porfolio {
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  column-gap: 25px;
}
.trabajos-porfolio .card {
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.trabajos-porfolio .card .porfolio-item {
  position: absolute;
}
.trabajos-porfolio .card .porfolio-item i {
  font-size: 80px;
  color: #fff;
}
.trabajos-porfolio .card img {
  width: 100%;
  border-radius: 15px;
}
.trabajos-porfolio .card:hover {
  cursor: pointer;
}

.contacto {
  background-image: url(../img/oficina.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  margin: 0 20px;
  padding: 100px;
}
.contacto body.darkmode .contacto {
  background-image: url(../img/oficina2.jpg);
}
.contacto .contenedor-form {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 10px;
}
.contacto .contenedor-form .datos-contacto {
  grid-column: span 2;
  display: flex;
  flex-direction: column;
}
.contacto .contenedor-form .datos-contacto h3 {
  position: relative;
  top: -50px;
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.contacto .contenedor-form .datos-contacto h3 div.animacion {
  transform: translateY(-60px);
}
.contacto .contenedor-form .datos-contacto h3 div {
  display: flex;
  flex-direction: column;
  position: relative;
  transition: all 0.3s ease;
}
.contacto .contenedor-form .datos-contacto h3 div .segunda-linea {
  position: absolute;
  top: 60px;
}
.contacto .contenedor-form .datos-contacto ul {
  list-style: none;
  position: relative;
  top: -100px;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  column-gap: 10px;
}
.contacto .contenedor-form .datos-contacto ul .iconos {
  grid-column: span 1;
  display: flex;
  flex-direction: column;
  text-align: center;
  vertical-align: middle;
  margin: 5px 0;
}
.contacto .contenedor-form .datos-contacto ul .iconos i {
  margin: 5px 0;
  font-size: 15px;
}
.contacto .contenedor-form .datos-contacto ul .datos-iconos {
  grid-column: span 9;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 5px 0;
}
.contacto .contenedor-form .datos-contacto ul .datos-iconos li {
  margin: 5px 0;
  font-size: 15px;
}
.contacto .contenedor-form .datos-contacto .redes-sociales {
  position: relative;
  top: -100px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  line-height: 40px;
}
.contacto .contenedor-form .datos-contacto .redes-sociales a {
  margin: 0 20px;
  color: #000;
  font-size: 20px;
}
body.darkmode .contacto .contenedor-form .datos-contacto .redes-sociales a {
  color: #fff;
}
.contacto .contenedor-form .datos-contacto .redes-sociales a i {
  width: 100%;
}
.contacto .contenedor-form .datos-contacto .redes-sociales a:hover {
  color: rgba(255, 193, 0, 0.8784313725);
}
.contacto .contenedor-form .formulario {
  grid-column: span 3;
  width: 100%;
}
.contacto .contenedor-form .formulario input, .contacto .contenedor-form .formulario textarea {
  display: block;
  width: 92%;
  padding: 10px;
  border: 2px solid rgba(255, 193, 0, 0.8784313725);
  border-radius: 10px;
  margin: 10px 0;
}
.contacto .contenedor-form .formulario textarea {
  min-height: 120px;
  max-height: 300px;
  min-width: 92%;
  max-width: 92%;
}
.contacto .contenedor-form .formulario .contenedor {
  padding: 20px;
}
.contacto .contenedor-form .formulario .contenedor .btn-formulario {
  margin: 0 15px;
