  grid-template-columns: repeat(5, 1fr);
}
footer .footer__nombre {
  grid-column: span 3;
  line-height: 60px;
  text-align: center;
  vertical-align: middle;
  color: #fff;
  font-weight: 400;
  font-size: 20px;
}
footer .footer__nombre span {
  font-size: 15px;
  font-style: italic;
}
footer .footer__redes-sociales {
  grid-column: span 1;
  line-height: 60px;
  text-align: center;
  vertical-align: middle;
}
footer .footer__redes-sociales a {
  margin: 15px;
  font-size: 20px;
  color: #fff;
}
footer .footer__redes-sociales a:hover {
  color: rgba(255, 193, 0, 0.8784313725);
}
footer .footer__flecha-arriba {
  display: none;
  grid-column: span 1;
  line-height: 60px;
  text-align: right;
  vertical-align: middle;
  margin-right: 50px;
}
footer .footer__flecha-arriba a {
  color: #fff;
  font-size: 20px;
}
footer .footer__flecha-arriba a:hover {
  color: rgba(255, 193, 0, 0.8784313725);
}

@media screen and (max-width: 950px) {
