  }
}
@media screen and (max-width: 780px) {
  .contenedor__menu {
    background-color: rgba(255, 193, 0, 0.8784313725);
    top: 0;
  }
  .contenedor__menu .menu__izq .titulo__menu {
    color: #000;
  }
  .contenedor__menu .menu__der {
    width: 30%;
    margin: 0px;
    padding: 5px;
    flex-direction: column;
    justify-content: left;
    background-color: rgba(255, 193, 0, 0.4901960784);
    position: fixed;
    right: 0;
    top: 35px;
    height: 0;
    transition: all 0.3s ease;
    opacity: 0;
    visibility: hidden;
  }
  .contenedor__menu .menu__der li {
    margin: 0;
    border-bottom: 1px solid #000;
  }
  .contenedor__menu .menu__der li a {
    font-size: 15px;
    font-weight: 500;
  }
  .contenedor__menu .menu__der li a:hover {
    transform: scale(1);
  }
  .contenedor__menu .icono_m {
    display: block;
    opacity: 1;
  }
  .contenedor__menu input:checked ~ ul {
    height: 120px;
    visibility: visible;
    opacity: 1;
  }
  .contenedor__menu input:checked ~ .icono_m {
    display: none;
    opacity: 0;
  }
  .contenedor__menu input:checked ~ .icono_x {
    display: block;
    opacity: 1;
  }
  .portada .marco__sup {
