  }
  .portada .marco__der {
    display: none;
  }
  .portada .marco__izq {
    display: none;
  }
  .portada .marco__inf {
    display: none;
  }
  .portada:hover {
    background-position: left !important;
  }
  .portada:hover .contenedor h1 {
    transition-property: none;
    transform: scale(1) !important;
  }
  .portada:hover .contenedor h2 {
    transition-property: none;
    transform: scale(1) !important;
  }
  .portada:hover .contenedor button {
    transition-property: none;
    transform: scale(1) !important;
  }
  .separador-section .tecnologias {
    padding: 0 15px;
