  }
  .separador-section .iconos-tecnologias li {
    font-size: 25px;
  }
  .separador-section .efecto-ola {
    left: 0;
    width: 100%;
  }
  .porfolio .trabajos-porfolio {
    grid-template-columns: repeat(2, 1fr);
  }
  #modales__listado .modal .modal__contenedor {
    width: 70%;
  }
  .contacto {
    margin: 0px;
  }
  footer {
    margin: 0px;
  }
  footer .footer__flecha-arriba {
    display: block;
    margin-right: 30px;
  }
}
