.contenedor__menu .menu__der li a {
  transition: all 0.3s linear;
}
.contenedor__menu .menu__der li a:hover {
  font-weight: 700;
  color: #61CE70;
  transform: scale(1.1);
}

.portada {
  transition: background-position 4s ease;
}
.portada .contenedor button:hover {
  background: rgba(16, 32, 154, 0.2509803922);
}
.portada .contenedor a {
  transition: transform 1s ease;
}
.portada .contenedor a:hover {
  transform: scale(1.2);
}
.portada .marco__sup {
  transition: all 1s ease;
}
.portada .marco__inf {
  transition: all 1s ease;
}
.portada .marco__der {
  transition: all 1s ease;
}
.portada .marco__izq {
  transition: all 1s ease;
}

.portada:hover {
  background-position: bottom;
}
.portada:hover .contenedor h1 {
  transition: all 2s ease;
  transform: scale(1.11);
}
.portada:hover .contenedor h2 {
  transition: all 2s ease;
  transform: scale(1.15);
}
.portada:hover .contenedor .boton {
  transition: transform 2s ease;
  transform: scale(1.05);
}

.separador-section .iconos-tecnologias li i {
  transition: all 0.5s ease;
}
.separador-section .iconos-tecnologias li i:hover {
  transform: rotate(360deg);
  cursor: pointer;
}

.trabajos-porfolio .card img {
  transition: opacity 0.3s ease;
}
.trabajos-porfolio .card img:hover {
  opacity: 0.75;
}

.modal {
  transition: opacity 0.6s;
}
.modal .modal__contenedor {
  transform: translateY(-110vh);
  transition: transform 0.8s ease 0.8s;
}

.modal--show {
  transition: opacity 0.6s;
}
.modal--show .modal__contenedor {
  transform: translateY(0);
  transition: transform 0.8s ease 0.8s;
}

.footer__nombre {
  transition: all 2s ease;
  opacity: 0;
  transform: translate(-500px, 0);
}

.footer__redes-sociales {
  transition: all 1.5s ease;
  opacity: 0;
  transform: translate(-300px, 0);
}

.footer__flecha-arriba {
  transition: all 1.5s ease;
  opacity: 0;
  transform: translate(-300px, 0);
}

.modal {
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(17, 17, 17, 0.7411764706);
  display: flex;
  opacity: 0;
  pointer-events: none;
}
.modal .modal__contenedor {
  width: 50%;
  border-radius: 6px;
  margin: auto;
  background: #fff;
  display: grid;
  gap: 0.5em;
  place-items: center;
  grid-auto-columns: 100%;
}
body.darkmode .modal .modal__contenedor {
  background: #222;
}
.modal .modal__contenedor .parte_superior {
  width: 100%;
  height: 30px;
  background: rgba(255, 193, 0, 0.4901960784);
  border-radius: 6px 6px 0 0;
}
body.darkmode .modal .modal__contenedor .parte_superior {
  background: rgba(255, 193, 0, 0.8784313725);
}
.modal .modal__contenedor .parte_superior div {
  position: absolute;
  top: 5px;
  left: 15px;
  visibility: hidden;
}
.modal .modal__contenedor .parte_superior div a {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  margin: 0 2px;
}
body.darkmode .modal .modal__contenedor .parte_superior div a {
  color: #000;
}
.modal .modal__contenedor .parte_superior .modal__close {
  text-decoration: none;
  position: absolute;
  top: 5px;
  right: 15px;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
}
body.darkmode .modal .modal__contenedor .parte_superior .modal__close {
  color: #000;
}
.modal .modal__contenedor .modal__titulo {
  font-size: 20px;
}
.modal .modal__contenedor .modal__img {
  width: 65%;
  opacity: 0.9;
  max-width: 400px;
  border-radius: 6px;
}
.modal .modal__contenedor .parte_inferior {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 25px 0;
}
.modal .modal__contenedor .parte_inferior .modal__parrafo {
  margin: 15px;
}
.modal .modal__contenedor .parte_inferior .modal__parrafo span {
  font-style: italic;
}
.modal .modal__contenedor .parte_inferior a {
  margin: 20px;
  text-decoration: none;
}

.modal--show {
  opacity: 1;
  pointer-events: unset;
}